import React from 'react';
import PropTypes from 'prop-types';

class GoodsList extends React.PureComponent {
  render() {
    return (
      <ul>
        {
          this.props.dataForRender.map(data => (<li key={data}>{data}</li>))
        }
      </ul>
    );
  }
}

GoodsList.propTypes = {
  dataForRender: PropTypes.arrayOf(
    PropTypes.string,
  ).isRequired,
};

export default GoodsList;
